.task-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 16px;
  table-layout: auto; 
  
}

.task-table th,
.task-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.task-no{
  /* background-color: rgb(19, 19, 245);
  color: white;
  border-radius: 30%;
  padding: 5px 2em; */
  padding: 6px 12px;
  font-size: 10px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  flex-shrink: 0; /* Prevents buttons from shrinking */
}
/* .task-table th {
  background-color: #f4f4f4;

  font-weight: bold;
} */
.task-table thead th {
  position: sticky;
  top: 0;
  background-color: #d3d0d0;
  z-index: 1;
  padding: 10px;
  text-align: left;
  font-weight: bold;
}
.task-table tbody tr.selected {
  background-color: #d3e3fd; /* Light blue background for selected rows */
}