
/* AssignTask.css */
.assign-task-container {
    display: flex;
    flex-direction: column; /* Change to column for smaller screens */
    height: 100vh;
  }
  
  .content {
    flex: 1;
    /* padding: 10px; */
    width: 100%; /* Adjust width for smaller screens */
  }
  
  .content-body {
    margin-top: 10px;
    background-color: #f9f9f9;
    border-radius: 5px;
    border: 1px solid #ddd;
    font-size: 14px;
    /* overflow-x: auto; Enable horizontal scrolling for smaller screens */
  }
  
  /* Responsive table */
  .task-table-container {
    width: 100%;
    max-height: 510px;
    overflow-x: auto; 
    overflow-Y: auto; 
  }
  
  .task-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 16px;
    table-layout: auto; 
    
  }
  
  .task-table th,
  .task-table td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  /* .task-table th {
    background-color: #f4f4f4;
  
    font-weight: bold;
  } */
  .task-table thead th {
    position: sticky;
    top: 0;
    background-color: #d3d0d0;
    z-index: 1;
    padding: 10px;
    text-align: left;
    font-weight: bold;
  }
  .task-table tbody tr.selected {
    background-color: #d3e3fd; /* Light blue background for selected rows */
  }
  /* .task-table tr:hover {
    background-color: #f1f1f1;
  } */

  /* Input group styling */
  .input-container {
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex: 0;
  }
  
  .input-container label {
    font-size: 12px;
    font-weight: bold;
  }
  
  .input-group {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
  }
  
  .input-group .input-container {
    flex: 1;
  }
  
  .input-group input {
    padding: 8px;
    font-size: 14px;
  }
  
  .input-group button {
    padding: 8px 12px;
  }
  
  /* Responsive adjustment for mobile */
  @media screen and (max-width: 600px) {
    .input-group {
      flex-direction: column;
    }
    .input-group .input-container {
        width: 100%;
      }
      .input-group input {
        font-size: 12px;
      }
    
      .input-group button {
        width: 100%;
        font-size: 12px;
      }
    
      .input-container label {
        font-size: 10px;  /* Smaller label text on mobile */
      }
  }
  
  
  input[type="checkbox"] {
    cursor: pointer;
  }
  
  /* Mobile-first adjustments */
  @media screen and (max-width: 768px) {
    .assign-task-container {
      flex-direction: column; /* Stack sidebar and content vertically */
    }
  
    .sidebar {
      flex-direction: column; /* Stack buttons vertically */
      gap: 5px;
    }
  
    .sidebar button {
      font-size: 12px;
      padding: 8px;
    }
  
    .header {
      font-size: 12px;
      padding: 5px 10px;
    }
  
    .content-body {
      font-size: 12px;
    }
  
    .task-table th, .task-table td {
      font-size: 12px;
      padding: 8px;
    }
  }
  
  /* For very small screens */
  @media screen and (max-width: 480px) {
  
    .task-table th, .task-table td {
      padding: 5px;
      font-size: 10px;
    }
  }
  