.navbar {
  display: flex;
  justify-content: center;
  gap: 8px;
  padding: 8px;
  width: 100%;
  background-color: #f0f0f0;
  border-bottom: 1px solid #ccc;
  overflow-x: auto; /* Allows horizontal scrolling if needed */
  white-space: nowrap;
}

.navbar button {
  padding: 6px 12px;
  font-size: 14px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  flex-shrink: 0; /* Prevents buttons from shrinking */
}

.navbar button:hover {
  background-color: #0056b3;
}

/* Optional: Tweak button size slightly for very small screens */
@media (max-width: 480px) {
  .navbar button {
    padding: 5px 10px;
    font-size: 12px;
  }
}
